.cb-home-no-obligation {
	font-size: 44px;
	font-weight: bold;
	text-align: center;
	width: 100%;
	line-height: 1.2;
	color: #212529;
	padding: 1rem;
	font-family: "Poppins", sans-serif !important;

	margin: 0 auto;
	width: 100%;
	max-width: 1030px;
	padding: 0 15px;
  }
  
  .cb-home-no-obligation::before,
  .cb-home-no-obligation::after {
	content: "";
	display: block;
	width: 100%;
	border: 1px solid #808080;
	background-color: currentColor;
	margin: 35px 0;
  }
/*   
  .cb-home-no-obligation::after {
	margin-top: 2rem;
	margin-bottom: 0;
  } */
  @media (min-width: 1024px) {
	.cb-home-no-obligation {
		font-size: 50px;
		padding: 0;
	  }
  }